
import { mapActions } from 'vuex'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'


export default {
data () {
return {
MindInfo: '',
formState: '',
Current: {},
HistoryMind: []
}
},
mounted () {
this.getStopFlowData()
},
methods: {

submitData () {
let valid = this.validSubmitData()

if (!valid.isPass) {
this.AlertShowEvent(valid.msg)
return false
}

let params = Object.assign({}, {
Current: this.Current,
FlowOperate: 'Stop',
MindInfo: this.MindInfo,
VoteText: '',
VoteValue: ''
})

this.FlowActionData(JSON.stringify(params)).then((response) => {
this.ToastShowEvent('完成提交')
this.GetInformCount().then(() => {
if (this.timer) {
clearTimeout(this.timer)
}

this.timer = setTimeout(() => {
if (this.formState === 'view') {
this.$router.push('/workinfos')
} else {
this.goBack()
}
}, 1000)
}).catch((e) => {
this.AlertShowEvent(e.message)
})
}).catch((e) => {
this.AlertShowEvent(e.message)
})
},

validSubmitData () {
let obj = {
msg: '',
isPass: true
}

if (this.MindInfo === '') {
obj.msg = '意见不能为空'
obj.isPass = false
return obj
}

return obj
},

getStopFlowData () {
this.getQuery()
let params = Object.assign({}, {
SubOperate: 'GetCurrentSeque',
FlowOperate: 'SupplyInstance',
SequeID: this.query.SequeID,
Current: this.query
})

this.FlowActionData(JSON.stringify(params)).then((response) => {
let value = response.data.value
this.Current = Object.assign({}, value.Current)
this.HistoryMind = value.HistoryMind.concat()
}).catch((e) => {
this.AlertShowEvent(e.message)
})
},

getQuery () {
let query = this.$router.history.current.query
if (query) {
this.query = Object.assign({}, query)
this.formState = this.query.FormState
}
},

goBack () {
this.$router.back()
},
...mapActions([
'GetInformCount',
'FlowActionData',
'AlertShowEvent',
'ToastShowEvent'
])
},
components: {
XTextarea,
Group
}
}



